import React from 'react';
import styles from './Works.module.css'
import WorksItem from "./WorksItem/WorksItem";
import Title from "../Title/Title";
import counterImg from "../../image/2.png";
import walletImg from "../../image/3.png";
import Fade from 'react-reveal/Fade';

const Works = (props) => {

    let state = {
        projectName: ['Counter', 'Expense calculator'],
        projectLink: ['https://i1-kmv.github.io/hardCounter/', 'https://i1-kmv.github.io/Present-calc/']
    }

    const backCount = {
        backgroundImage: `url(${counterImg})`
    };
    const backWallet = {
        backgroundImage: `url(${walletImg})`
    }


    return (
        <div className={styles.works}>

            <div className={styles.container} id={'projects'}>
                <Title name={props.name}/>
                <Fade bottom>
                    <div className={styles.works__items}>
                        <WorksItem style={backCount} link={state.projectLink[0]} projectName={state.projectName[0]}/>
                        <WorksItem  style={backWallet}  link={state.projectLink[1]} projectName={state.projectName[1]}/>
                    </div>
                </Fade>
            </div>

        </div>
);
}

export default Works;
